.featured {
    width: 100%;
}

.featured .flex {
    margin: 50px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.2rem;
    padding: 1rem;
}
.word h1{
    text-align: center;
    font-size: 2rem;
}

.word h4{
    /* padding-inline: 30px; */
    margin-top: 10px;
    text-align: center;
    font-family: cursive,serif;
    color: rgba(0, 0, 0, 0.432);
}
.flex img {
    width: 100%;
}

.flex h4{
    text-align: center;
    color: rgba(0, 0, 0, 0.486);
}
.flex h3{
    text-align: center;
}

span{
    margin: 20px;
    color: rgba(0, 0, 0, 0.534);
}

@media screen and (max-width:940px) {
    .featured {
        margin: 1rem 0;
    }

    .featured .flex {
        grid-template-columns: 1fr;
        grid-gap: .5rem;
        margin: 0;
    }
    span{
        display: flex;
        justify-content: center;
        font-size: 20px;
    }
    .word h1{
        margin-top: 50px;
        font-size: 1.2rem;
        /* margin-top: 10px; */
        text-align: center;
    }
}